import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { TodoList as TodoItemComponent } from './TodoList'
import {
  changeTodo,
  deleteTodo,
  doneTodo,
  getTodoList,
} from 'Store/action'


function mapStateToProps ({todoApp, filter}) {

  const filteredTodo =  todoApp.todoList.filter(todo => filter.filteringTodo === 'all' || todo.completed)
  return { todos: filteredTodo }
}

function mapDispatchToProps (dispatch) {
  return {
    getTodoList: bindActionCreators(getTodoList, dispatch),
    doneTodo: bindActionCreators(doneTodo, dispatch),
    changeTodo: bindActionCreators(changeTodo, dispatch),
    deleteTodo: bindActionCreators(deleteTodo, dispatch),
  }
}

export const TodoList = connect(mapStateToProps, mapDispatchToProps)(TodoItemComponent)




