import types from 'Constants'

const initialState = {
  fetching: false,
  todoList: [],
}

export const todos = (state = initialState, action) => {
  switch (action.type) {

    case types.FETCH_TODO_REQUEST :
      return {
        ...state,
        fetching: action.fetching,
      }

    case types.FETCH_TODO_SUCCESS :
      return {
        ...state,
        todoList: action.todoList,
      }

    case types.FETCH_TODO_FAILURE :
      return {
        ...state,
        error: action.error,
      }

    case types.ADD_TODO:

      return {
        ...state,
        todoList: [...state.todoList, action.newItem]
      }

    case types.DELETE_TODO:
      return {
        ...state,
        todoList: state.todoList.filter(item => item.id !== action.id)
      }

    case types.DONE_TODO:

      return {
        ...state,
        todoList: state.todoList.map(item =>
          item.id === action.id ? {...item, completed: !item.completed} : item)
      }

    case types.CHANGE_TODO:

      return {
        ...state,
        todoList: state.todoList.map(item =>
          item.id === action.id ? {...item, title: action.text} : item)
      }

    default:
      return state
  }
}

