import React, { Component } from 'react'
import List from '@material-ui/core/List'
import {withStyles} from '@material-ui/core/styles'
import { API_PATH } from 'API'
import { TodoItem } from 'Components/TodoItem'

const styles = theme => {
  // console.log(theme)
  return ({
    animation: {
      fadeEnter: theme.myVar.animation.fade['fade-enter'],
      fadeEnterActive: theme.myVar.animation.fade['fade-enter-active'],
      fadeExit: theme.myVar.animation.fade['fade-exit'],
      fadeExitActive: theme.myVar.animation.fade['fade-exit-active']
    }
  })
}

export class TodoListComponent extends Component {
  state = {
    modalInputValue: '',
    validateModal: false,
    editInputValue: '',
    validateEdit: false,
    editTodoId: 0,
  }

  componentDidMount () { this.props.getTodoList(API_PATH) }

  _editTodo = (e) => {
    const value = e.target.value
    this.setState({
      editInputValue: value,
      validateEdit: !!value,
    })
  }

  inputHandlers = { editTodo: 'editTodo'}

  inputHandlerMap = { [this.inputHandlers.editTodo]: this._editTodo }

  _handleChangeMap = (type) => (e) => this.inputHandlerMap[type](e)

  _toggleDone = (item) => this.props.doneTodo(item.id)

  _openEdit = (item) => {
    this.setState({
      editTodoId: item.id,
      editInputValue: item.title,
      validateEdit: false,
    })
  }

  _closeEdit = () => this.setState({editTodoId: 0})

  _editDone = (item) => {
    this.props.changeTodo(item.id, this.state.editInputValue)
    this.setState({editTodoId: 0})
  }

  _delete = (item) => this.props.deleteTodo(item.id)

  clickHandlers = {
    toggleDone: 'toggleDone',
    openEdit: 'openEdit',
    closeEdit: 'closeEdit',
    editDone: 'editDone',
    delete: 'delete',
  }

  clickHandlerMap = {
    [this.clickHandlers.toggleDone]: this._toggleDone,
    [this.clickHandlers.openEdit]: this._openEdit,
    [this.clickHandlers.closeEdit]: this._closeEdit,
    [this.clickHandlers.editDone]: this._editDone,
    [this.clickHandlers.delete]: this._delete,
  }

  _handleClick = (type, item) => () => this.clickHandlerMap[type](item)

  render () {
    const {todos} = this.props
    const {editTodoId, validateEdit} = this.state

    return (
      <List>
          {todos.map(item => (
              <TodoItem key={item.id}
                        editTodo={editTodoId === item.id}
                        item={item}
                        handleChange={this._handleChangeMap(this.inputHandlers.editTodo)}
                        validate={validateEdit}
                        handleClick={this._handleClick}
                        clickHandlers={this.clickHandlers}
              />
          ))}
      </List>
    )
  }

}

export const TodoList = withStyles(styles)(TodoListComponent)

