import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'
import Grid from '@material-ui/core/Grid'
import BottomNavigation from '@material-ui/core/BottomNavigation'
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction'
import DoneAllOutlined from '@material-ui/icons/DoneAllOutlined'
import ViewList from '@material-ui/icons/ViewList'
import Badge from '@material-ui/core/Badge'

const styles = (theme) => ({
  root: {
    width: theme.myVar.size['300'],
    marginTop: theme.myVar.size['50']
  },
  badge: {
    padding: `0 ${theme.spacing.unit * 2}px`,
    background: 'transparent'
  },
  active: {
    background: theme.palette.grey['300']
  },
})

class FilterComponent extends Component {

  handleChange = (event, value) => {
    this.props.filter(value)
  }

  render() {
    const {done, length, filteringTodo, classes} = this.props

    const renderLabelAll =
      <Badge className={classes.badge} color="primary" badgeContent={length}>
        <ViewList/>
      </Badge>

    const renderLabelDone =
      <Badge className={classes.badge} color="primary" badgeContent={done}>
        <DoneAllOutlined/>
      </Badge>

    return (
      <Grid container justify={'center'}>
        <BottomNavigation
          value={filteringTodo}
          onChange={this.handleChange}
          showLabels
          className={classes.root}
        >
          <BottomNavigationAction classes={{selected: classes.active}}
                                  label={renderLabelAll}
                                  value="all"
          />
          <BottomNavigationAction classes={{selected: classes.active}}
                                  label={renderLabelDone}
                                  value="done"
          />
        </BottomNavigation>
      </Grid>
    )
  }
}

export const Filter = withStyles(styles)(FilterComponent)
