import React, { Component } from 'react'
import Grid from '@material-ui/core/Grid'
import { withStyles } from '@material-ui/core/styles'
import { Modal } from 'Components/Modal'
import { TodoList } from 'Components/TodoList'
import { Filter } from 'Components/Filter'
import DialogContent from '@material-ui/core/DialogContent'
import TextField from '@material-ui/core/TextField'
import DialogActions from '@material-ui/core/DialogActions'
import Button from '@material-ui/core/Button'

const styles = theme => ({
  inputStyles: {
    'width': theme.myVar.size['300'],
  },
})

export class TodoGridComponent extends Component {
  state = {
    modalValue: ''
  }

  _closeModal = () => { this.props.showModal(false) }

  _addTodo = () => {
    const {addTodo, showModal} = this.props
    addTodo(this.state.modalValue)
    showModal(false)
  }

  _handleChangeText = (e) => this.setState({ modalValue: e.target.value })

  render () {
    const {modalOpen, classes} = this.props
    return (
      <div>
        <Grid container justify="center">
          <Grid item md={6}>
            <Filter/>
            <TodoList/>
          </Grid>
        </Grid>

        <Modal open={modalOpen}
               title={'Add new todo'}
               handleClose={this._closeModal}>
          <DialogContent>
            <TextField className={classes.inputStyles}
                       autoFocus
                       margin="dense"
                       id="name"
                       label="Todo title"
                       type="text"
                       onChange={this._handleChangeText}
                       fullWidth />
          </DialogContent>
          <DialogActions>
            <Button onClick={this._closeModal} color="primary">
              Cancel
            </Button>
            <Button onClick={this._addTodo} color="primary">
              Add
            </Button>
          </DialogActions>
        </Modal>

      </div>
    )
  }
}

export const TodoGrid = withStyles(styles)(TodoGridComponent)


