import types from 'Constants'
import { generateRandomNumber } from 'Helpers'

export function getTodoList (url) {
  return (dispatch) => {
    dispatch(isLoadTodoList(true))

    fetch(url).then(response => {
      if (!response.ok) {
        throw Error(response.statusText)
      }
      dispatch(isLoadTodoList(false))
      return response
    })
    .then(response => response.json())
    .then(response => dispatch(loadTodoData(response)))
    .catch(error => dispatch(loadTodoError(true, error)))
  }
}

export function loadTodoError (bool, error) {
  console.log(error)
  return {
    type: types.FETCH_TODO_FAILURE,
    error: bool,
  }
}

export function loadTodoData (data) {
  return {
    type: types.FETCH_TODO_SUCCESS,
    todoList: data,
  }
}

export function isLoadTodoList (bool) {
  return {
    type: types.FETCH_TODO_REQUEST,
    fetching: bool,
  }
}


export function addTodo (text) {
  return {
    type: types.ADD_TODO,
    newItem: {
      userId: 1,
      id: generateRandomNumber(10, 100),
      title: text,
      completed: false
    }
  }
}

export function deleteTodo (id) {
  return {
    type: types.DELETE_TODO,
    id
  }
}

export function doneTodo (id) {
  return {
    type: types.DONE_TODO,
    id,
  }
}

export function changeTodo (id, text) {
  return {
    type: types.CHANGE_TODO,
    text,
    id,
  }
}

