import React from 'react'
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles'

const theme = createMuiTheme({
  typography: {
    useNextVariants: true,
  },
  myVar: {
    size: {
      600: '600px',
      500: '500px',
      400: '400px',
      300: '300px',
      200: '200px',
      100: '100px',
      50: '50px',
      16: '16px',
      10: '10px',
    },
    animation: {
      fade: {
        'fade-enter': {opacity: 0.01},
        'fade-enter-active': {
          opacity: 1,
          transition: 'opacity 500ms ease-in'
        },
        'fade-exit': {opacity: 1},
        'fade-exit-active': {
          opacity: 0.01,
          transition: 'opacity 500ms ease-in'
        },
      }
    }
  }
})

export const MuiTheme = (props) => <MuiThemeProvider theme={theme}> {props.children} </MuiThemeProvider>
