import React from 'react'
import { withStyles } from '@material-ui/core/styles'
import { TodoItemEdit } from './TodoItemEdit'
import { TodoItemDefault } from './TodoItemDefault'

const style = theme => ({
  doneItem: {
    background: theme.palette.dark,
    textDecoration: 'line-through',
    textDecorationColor: theme.palette.common.black
  },
  item: {
    background: theme.palette.background.paper,
  },
})

const TodoItemComponent = (props) => {
  const {
    item,
    handleClick,
    editTodo,
    handleChange,
    validate,
    classes,
    clickHandlers
  } = props

  const activeClass = item.completed ? classes.doneItem : classes.item
  return (
    <React.Fragment>
      {
        editTodo ?
          <TodoItemEdit handleChange={handleChange}
                        clickHandlers={clickHandlers}
                        item={item}
                        handleClick={handleClick}
                        validate={validate}
                        activeClass={activeClass}
          /> :
          <TodoItemDefault handleClick={handleClick}
                           item={item}
                           activeClass={activeClass}
                           clickHandlers={clickHandlers}
          />
      }
    </React.Fragment>

  )
}

export const TodoItem = withStyles(style)(TodoItemComponent)
